// import uParse from  "../../components/uni-rich/parse.vue"
import topBar from  "../../components/topBar.vue"
import banner from  "../../components/banner.vue"

export default {
  components: {
    topBar,
    banner
  },
  data() {
    return {
      scrollTop: 0,                   // 距滚动距离
      isHeadShow: true,              // 头部作者名
      title: '',
      article_id: '',                               // 文章ID
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#ffffff',
      detail: {},
      userInfo: {},
      cultureInfo: {
        title: '2019深圳奶油田电音节',
        tags: ['深圳奶油田', '深圳'],
        time: '2019-05-16',
        words: '邀请了著名乐队Pendulum的核心成员Rob Swire和GaretMcGrillen改组成的双人电子音乐制作团队KnifeParty等，一系列世界级百慕大DJ及国际流行巨星齐现阵。一系列世界级百慕大DJ及国际流行巨星齐现阵。除了力为消费者带来前所未有的跟世界音乐巨星接触的机会，作为风暴电音节的主赞助商，随时随地可以去发现、体检、享受电音所带来的无限兴奋和快乐。'
      },
      swiperList: [],                          // 轮播图
      strings: [],
      // commentList: [{
      //   imgUrl: '',
      //   name: '撒浪嘿',
      //   speak: '终于有机会去一次音乐节，现场嗨爆了感觉人生已经到了高潮，哈哈。',
      //   time: '05-12',
      //   zan: 666,
      //   zan_status: 1,
      //   writerChild: [
      //     {
      //       imgUrl: '',
      //       name: '@看灰机',
      //       speak: '的确，现场太燃了，而且很多漂亮小姐姐。',
      //       time: '05-12',
      //       zan: 999,
      //       zan_status: 1
      //     }
      //   ]
      // }],               // 评论信息
      speakVal: '',     // 我的评论value值
      sumList: {
        zanTotal: 2000,
        starTotal: 1578,
        megTotal: 1959
      }
    }
  },
  // 接受首页传递的参数
  onLoad(option) {
    this.article_id = option.article_id
    this.getDetail(this.article_id)
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
        })
    },
    onScroll(e) {
      let that = this
      if(e.detail.scrollTop > that.scrollTop && that.isHeadShow) {
        that.isHeadShow= false
      } else if(e.detail.scrollTop < that.scrollTop && !that.isHeadShow){
        that.isHeadShow= true
      }
      that.scrollTop= e.detail.scrollTop
    },
    
    // 获取文章详情
    getDetail(id) {
      let params = {
        url: this.$api.articleDetail,
        data: {
        article_id: id
        }
      }
      this.$httpRequest(params).then((res) => {
        console.log(res.data.detail)
        this.detail = res.data.detail
      })
      
      // this.$http({
      //   cb: (err, res) => {
      //     console.log(res.data.detail)
      //     let detail = res.data.detail
      //     this.detail = detail
      //     this.swiperList = detail.banners
      //     // 文章标题等
      //     this.cultureInfo.title = detail.article_title
          
      //     this.userInfo = {
      //       file_path: detail.headimg.file_path,
      //       author: detail.author
      //     }
      //     var richtext =  detail.article_content
      //     const regex = new RegExp('img')
      //     richtext= richtext.replace(regex, `img style="max-width: 100%;"`)
          
      //     this.strings = richtext
          
          
      //     this.cultureInfo.time = detail.update_time
      //   }
      // })
    },
    gogoodDetail(item) {
      console.log(item)
      uni.navigateTo({
        url: './goodDetail?goods_id=' + item.goods_id
      })
    },
    // 评论点赞
    zanAction(item, index) {
      console.log('点赞', item, item.islike, index)
      let url = this.$api.commentunlike
      if(item.islike === 'no') {
        url = this.$api.commentlike
      }
      this.$http({
        url: url,
        data: {
          comment_id: item.id
        },
        cb: (err, res) => {
          if(!err && res) {
            switch(this.detail.comments_show[index].islike) {
              case 'yes':
                this.detail.comments_show[index].islike = 'no'
                this.detail.comments_show[index].likenum -= 1
                uni.showToast({
                	title: '取消点赞成功',
                  icon: 'none'
                })
                break
              case 'no':
                this.detail.comments_show[index].islike = 'yes'
                this.detail.comments_show[index].likenum += 1
                uni.showToast({
                	title: '点赞成功',
                  icon: 'none'
                })
                break
            }
          } else {
            switch(this.detail.comments_show[index].islike) {
              case 'yes':
                uni.showToast({
                	title: '取消点赞失败',
                  icon: 'none'
                })
                break
              case 'no':
                uni.showToast({
                	title: '点赞失败请重试',
                  icon: 'none'
                })
                break
            }
          }
        }
      })
    },
    
    // 分享
    goShare() {
      uni.share({
        provider: "weixin",
        scene: "WXSceneSession",
        type: 0,
        href: "http://uniapp.dcloud.io/",
        title: "uni-app分享",
        summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
        imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
        success: function (res) {
          console.log("success:" + JSON.stringify(res));
        },
        fail: function (err) {
          console.log("fail:" + JSON.stringify(err));
        }
      })
    },
    onInput(e) {
      this.speakVal = e.detail.value
    },
    // 发布评论
    addComment(e) {
      console.log(this.speakVal)
      this.$http({
        url: this.$api.addcomments,
        data: {
          article_id: this.article_id,
          comment: e.detail.value
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            // 成功后刷新数据
            this.getDetail(this.article_id)
            this.speakVal = ''
            uni.showToast({
            	title: '评论发布成功',
              icon: 'none'
            })
          } else {
            uni.showToast({
            	title: '发布失败请重试',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
